import React from 'react';
import { AboutVDE } from './about-vde/about-vde.component';
import { OurValues } from './our-values/our-values.component';
import { Sharing } from './sharing/sharing.component';
import { CSSTransition } from 'react-transition-group';
import ReactFullpage from '@fullpage/react-fullpage';
import { Header } from '../../ui/mobile-header/header.component';

const Animate = ({ transitionIn, transition = 'fade', ...props }) => (
  <CSSTransition
    in={transitionIn}
    unmountOnExit
    timeout={2000}
    classNames={transition}
  >
    <div className={transition} {...props} />
  </CSSTransition>
);

export const AboutContainer = () => {
  return (
    <ReactFullpage
      scrollOverflow={true}
      sectionsColor={['orange', 'purple', 'green']}
      render={({ state, fullpageApi }) => {
        return (
          <>
            <Header isSticky={true} />
            <div id="fullpage-wrapper">
              <div className="section">
                <AboutVDE />
              </div>
              <div className="section">
                <OurValues />
              </div>
              <div className="section">
                <Sharing />
              </div>
            </div>
          </>
        );
      }}
    />
  );
};
